.journal__main-content {
    display: flex;
}

.journal__sidebar {
    background-color: $dark-grey;
    color: #fff;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}

.journal__sidebar-navbar h3 {
    font-weight: lighter;

}

.journal__new-entry {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    &:hover {
        color: #aaa;
        transition: color .3s ease-in;
    }
}


.journal__entries{
    margin-top: 30px;
    flex: 1 1 auto;
    overflow-y: auto;

}

.journal__entry {
    background-color: #fff;
    border-radius: 5px;
    color: $dark-grey;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    overflow-y: hidden;
    cursor: pointer;
}

.journal__entry-picture {
    height: 75px;
    width: 75px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin: 2px;

}

.journal__entry-body {
    padding: 10px;
}

.journal__entry-title {
    font-size: 14px;
    font-weight: bold;
}

.journal__entry-content{
    font-size: 10px;
}

.journal__entry-date-box {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
    
}

.journal__entry-date-box span {
    font-size: 12px;
}